/* Tutor Panel */
.tutor-panel-outer{
  width: 100%;
}
.tutor-panel-inner{
  text-align: center;
}
.tutor-panel-right{
  margin-left: 1.5em;
}

/* Tutor Panel Home */
.tutor-panel-main{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.tutor-panel-left, .tutor-panel-right{
  margin: 2em;
}
.tutor-panel-left{
  max-width: 600px;
  min-width: 300px;
}
.tutor-panel-right{
  max-width: 400px;
  min-width: 300px;
}

/* Create Question */
.create-question-form{
  padding: 2em;
  max-width: 100%;
  align-items: center;
  display: flex;
  flex-flow: column;
  text-align: left;
}
.create-question-flex{
  display: flex;
  justify-content: center;
}
.create-question-left{
  width: 300px;
}
.create-question-form h4{
  margin-bottom: .5em;
}
.create-question-body-header{
  position: relative;
  display: inline-block;
}
.latex-tooltip{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 18px;
  height: 18px;
  color: white;
  background: var(--primary-1);
  border-radius: 9px;
  position: absolute;
  right: -1.5em;
  top: -.5em;
  font-size: 75%;
}
.create-question-left-section{
  margin-bottom: 1em;
}
.create-question-right{
  margin-left: 2em;
}
.math-textarea-outer{
  width: 100%
}
.math-textarea{
  min-height: 200px;
}
.create-question-answer-outer{
  display: flex;
  align-items: center;
}
.create-question-answer[type=text]{
  margin-bottom: .25em;
  width: 150px;
  max-width: 100%;
  border-color: var(--red-60);
}
.create-question-answer.first-answer[type=text]{
  border-color: var(--green-60);
}
.add-answer{
  width: 150px;
  max-width: 100%;
  background: var(--grey-95);
  padding: .25em;
  margin-bottom: .5em;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  cursor: pointer;
}
.add-answer:hover{
  background: var(--grey-80);
}
.create-question-preview-body{
  width: 30ch;
}
.create-question-preview-body p{
  text-align: left;
}
.create-question-preview-body .math-block{
  text-align: center;
}
.create-question-answer-preview{
  padding: .5em 1em;
  margin-bottom: .5em;
  border: 1px solid var(--grey-80);
  border-radius: 5px;
}

/* Set List */
.set-list-outer{
  display: flex;
  flex-flow: column;
  align-items: center;
}
.set-list-header-cont{
  display:inline-flex;
  align-items: center;
}
.set-list-header-cont h1{
  margin-bottom: 0;
}
.set-list-header-plus{
  margin-left: 1em;
  color: var(--primary-1);
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.no-sets-outer{
  margin-top: 1em;
  max-width: 20ch;
}
.set-list-inner{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.set-list-item{
  border: 1px solid var(--grey-80);
  border-radius: 5px;
  margin: 1em;
  max-width: 100%;
  width: 250px;
  position: relative;
  cursor: pointer;
}
.set-list-item-content{
  padding: 1.5em;
  width: 100%;
  display: block;
}
.set-list-item-content:hover{
  background: var(--grey-95);
}
.set-list-item-title{
  margin-bottom: 0;
}
.set-list-item-date{
  font-size: 85%;
}
.set-list-item-controls{
  position: absolute;
  top: 1.25em;
  right: 1.25em;
}
.set-list-item-controls .control-icon{
  width: 27px;
  height: 27px;
  color: var(--grey-70);
  padding: .25em;
  position: relative;
}
.set-list-item-controls .control-icon:hover{
  color: var(--grey-20);
}


/* Micro Popup */
.micro-popup{
  background: white;
  border: 1px solid var(--grey-90);
  position: absolute;
  border-radius: 5px;
  top: calc(-1.5em + 3px);
  width: 160px;
  left: calc(1.5em + 35px);
  box-shadow: 0 3px 15px 0 rgba(0, 0, 0, .1);
  z-index: 2;
}
.micro-popup div{
  display: flex;
  align-items: center;
  padding: .45em .5em;
}
.micro-popup div:hover{
  background: var(--grey-95);
}
.micro-popup-edit{
  color: #4a92f0;
}
.micro-popup-delete{
  color:  #f04a4a;
}
.micro-popup-icon{
  margin-right: .5em;
}

/* Individual Set */
.set-title-cont{
  margin-bottom: 1em;
}
.question-list-item{
  padding: 3em 2em 2em;
  border: 1px solid var(--grey-80);
  border-radius: 5px;
  max-width:  600px;
  width: 100%;
  position: relative;
  margin: 0 auto 1em;
}
.question-list-item-number{
  position: absolute;
  top: 1em;
  left: 1em;
}
.question-list-item-dots{
  position: absolute;
  top: .75em;
  right: .75em;
  color: var(--grey-70);
  padding: .25em;
  cursor: pointer;
}
.question-list-item-dots:hover{
  color:  var(--grey-20);
}
.question-list-item-body{
  text-align: left;
}
.question-list-item-answer-cont{
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.question-list-item-answer{
/*  display: flex;
  align-items: center;
*/  padding: .75em;
  border-radius: 5px;
  margin: .3em;
  border: 1px solid white;
}
.question-list-item-answer-right{
  /*background: var(--green-90);*/
  border-color: var(--green-50);
  color:  var(--green-50);
}
.question-list-item-answer-wrong{
/*  background: var(--red-90); */
  border-color: var(--red-60);
  color:  var(--red-60);
}
.question-list-create{
  padding: 1em;
  /*border: 1px solid var(--grey-80);*/
  border-radius: 5px;
  margin-bottom: 2em;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background: var(--primary-1);
  color: white;
}
.question-list-create:hover{
  background: var(--primary-2);
}
.question-list-create-plus{
  width: 25px;
  height: 25px;
  margin-right: .3em;
}

/* Tutor set nav */
.tutor-set-nav{
  display: flex;
  justify-content: center;
}
.tutor-set-nav a{
  margin: 0 .5em;
}
.tutor-set-nav a.selected{
  opacity: 1;
}

/*Create Set*/
.create-set-set-name-input{
  margin-bottom: 1m em;
}

/* Student Interface */
/* Add Student */
.add-student-results-cont{
  display: flex;
  align-items: center;
  flex-flow: column;
}
.add-student-student{
  padding: 1.25em;
  border: 1px solid var(--grey-80);
  border-radius: 5px;
  display: flex;
  align-items: center;
  margin-bottom: 1em;
  cursor:  pointer;
  max-width: 100%;
  width: 17em; 
}
.add-student-student:hover{
  background: var(--grey-95);
}
.add-student-student-right{
  text-align: left;
  margin-left: 1em;
}
.add-student-displayname{
  margin-bottom: .25em;
}
.add-student-student-username{
  margin: 0;
  color: var(--grey-50);
}
.add-student-student-icon{
  width: 50px;
  height: 50px;
  border-radius: 25px;
}

/*Student List*/
.student-list-item{
  display: flex;
  align-items: center;
  padding: 1em;
}
.student-list-item-left, .set-performance-student-left-left{
  width: 3em;
  height: 3em;
  border-radius: 1.5em;
  margin-right: 1em;
}
.student-list-item-right, .set-performance-student-left-right{
  text-align: left;
  max-width: 10em;
}
.student-list-item-right h3, .set-performance-student-left-right h3{
  margin-bottom: .15em;
}
.student-list-pending{
  opacity: .5;
}
.student-list-add-student-link{
  color: var(--primary-1);
  display: flex;
  align-items: center;
  justify-content: center;
}
.student-list-add-student-link:hover{
  color: var(--primary-2);
}
.student-list-add-student-link-add{
  margin-right: .5em;
}

/* Assign Set */
.assign-student-list-item{
  border: 1px solid var(--grey-80);
  border-radius: 5px;
  margin-bottom: .5em;
  cursor: pointer;
  word-break: break-word;
}
.assign-student-list-item:hover{
  border-color: var(--grey-50);
}
.assign-student-list-item.selected{
  border: 2px solid var(--primary-1);
}

/* Performance */
.set-performance-student-list{
  display: flex;
  flex-flow: column;
  align-items: center;
}
.set-performance-student, .view-student-performance-item{
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1.5em;
  border-radius: 10px;
  border: 1px solid var(--grey-50);
  margin-bottom: 1.5em;
  width: 100%;
  max-width: 600px;
}
.set-performance-student.incomplete, .view-student-performance-item.incomplete{
  opacity: .6; 
}
.set-performance-student-left{
  display: flex;
  align-items: center;
}
.set-performance-student-bar{
  width: 1px;
  height: 50px;
  background: var(--grey-80);
  margin: 0 2em;
}
.set-performance-student-right{
  text-align: center;
}
.set-performance-no-students-cont{
  
}

/* Review */
.tutor-rev-question-cont{
  display: flex;
  flex-flow: column;
  align-items: center;
  position: relative;
}
.tutor-rev-question{
  width: 30em;
  max-width: 100%;
  padding: 2rem;
}
.tutor-rev-question-body{
  text-align: left;
}
.tutor-rev-answer-block{
  list-style: none;
}
.tutor-rev-answer{
  padding: .5em .5em .5em 1em;
  color: white;
  text-align: center;
  border-radius: 5px;
  margin-bottom: .5em;
  text-align: left;
}
.tutor-rev-answer.incorrect{
  background: var(--red-60);
}
.tutor-rev-answer.correct{
  background: var(--green-50);
}

/* View student performance */

@media screen and (max-width: 600px){
  .set-performance-student, .view-student-performance-item{
    flex-flow: column;
    align-items: center;
  }
  .set-performance-student-bar{
    display: none;
  }
  .set-performance-student-left{
    margin-bottom: 1em;
  }
  .set-performance-student-left{
    flex-flow: column;
  }
  .set-performance-student-left-left{
    margin-bottom: 1em;
    margin-right: 0;
  }
  .set-performance-student-left-right{
    text-align: center;
  }
  .set-performance-student-right-bottom{
    margin-top: 1em;
  }
}