.katex-display {
	display: block;
	margin: 1em 0;
	text-align: center
}

.katex-display>.katex {
	display: inline-block;
	text-align: initial
}

.katex {
	font: normal 1.21em KaTeX_Math, KaTeX_Main, Times New Roman, serif;
	line-height: 1.2;
	white-space: nowrap;
	text-indent: 0;
	text-rendering: auto
}

.katex * {
	-ms-high-contrast-adjust: none!important
}

.katex .katex-html {
	display: inline-block
}

.katex .katex-mathml {
	position: absolute;
	clip: rect(1px, 1px, 1px, 1px);
	padding: 0;
	border: 0;
	height: 1px;
	width: 1px;
	overflow: hidden
}

.katex .base {
	position: relative
}

.katex .base,
.katex .strut {
	display: inline-block
}

.katex .textbf {
	font-weight: 700
}

.katex .textit {
	font-style: italic
}

.katex .textrm {
	font-family: KaTeX_Main
}

.katex .textsf {
	font-family: KaTeX_SansSerif
}

.katex .texttt {
